import React, { Component } from 'react';
import '../App.css';
import { observer } from 'mobx-react';

@observer
class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
      name: '',
      surname: '',
      email: ''
    };

    this.create = this.create.bind(this);
    this.update = this.update.bind(this);
    this.delete = this.delete.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  componentDidMount() {
    // get all entities - GET
    fetch("https://fairestdb.p.rapidapi.com/users/user", {
      "method": "GET",
      "headers": {
        "x-rapidapi-host": "fairestdb.p.rapidapi.com",
        "x-rapidapi-key": "b39b649d28msh5237f7a0c53af84p1f3fb2jsne97795af1f05"
      }
    })
    .then(response => response.json())
    .then(response => {
      this.setState({
        users: response
      })
    })
    .catch(err => { console.log(err);
    });
  }

  create(e) {
    // add entity - POST
    e.preventDefault();

    // creates entity
    fetch("https://fairestdb.p.rapidapi.com/users/user", {
      "method": "POST",
      "headers": {
        "x-rapidapi-host": "fairestdb.p.rapidapi.com",
        "x-rapidapi-key": "b39b649d28msh5237f7a0c53af84p1f3fb2jsne97795af1f05",
        "content-type": "application/json",
        "accept": "application/json"
      },
      "body": JSON.stringify({
        name: this.state.name,
        notes: this.state.notes
      })
    })
        .then(response => response.json())
        .then(response => {
          console.log(response)
        })
        .catch(err => {
          console.log(err);
        });
  }

  update(e) {
    // update entity - PUT
    e.preventDefault();

    // this will update entries with PUT
    fetch("https://fairestdb.p.rapidapi.com/users/user", {
      "method": "PUT",
      "headers": {
        "x-rapidapi-host": "fairestdb.p.rapidapi.com",
        "x-rapidapi-key": "b39b649d28msh5237f7a0c53af84p1f3fb2jsne97795af1f05",
        "content-type": "application/json",
        "accept": "application/json"
      },
      "body": JSON.stringify({
        _id: this.state.id,
        name: this.state.name,
        notes: this.state.notes
      })
    })
        .then(response => response.json())
        .then(response => { console.log(response);
        })
        .catch(err => { console.log(err); });
  }

  delete(e) {
    // delete entity - DELETE
    e.preventDefault();
    // deletes entities
    fetch(`https://fairestdb.p.rapidapi.com/users/user/_id/${this.state.id}`, {
      "method": "DELETE",
      "headers": {
        "x-rapidapi-host": "fairestdb.p.rapidapi.com",
        "x-rapidapi-key": "b39b649d28msh5237f7a0c53af84p1f3fb2jsne97795af1f05"
      }
    })
        .then(response => response.json())
        .then(response => {
          console.log(response);
        })
        .catch(err => {
          console.log(err);
        });
  }

  handleChange(changeObject) {
    this.setState(changeObject)
  }
  render() {
    return (
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-md-8">
              <form className="d-flex flex-column">
                <legend className="text-center">Add-Update-Delete User</legend>
                <label htmlFor="name">
                  User Name:
                  <input
                      name="name"
                      id="name"
                      type="text"
                      className="form-control"
                      value={this.state.name}
                      onChange={(e) => this.handleChange({ name: e.target.value })}
                      required
                  />
                </label>
                <label htmlFor="notes">
                  User surname:
                  <input
                      name="notes"
                      id="notes"
                      type="text"
                      className="form-control"
                      value={this.state.surname}
                      onChange={(e) => this.handleChange({ surname: e.target.value })}
                      required
                  />
                </label>
                <label htmlFor="id">
                  User ID:
                  <input
                      name="id"
                      id="id"
                      type="text"
                      className="form-control"
                      value={this.state.id}
                      onChange={(e) => this.handleChange({ id: e.target.value })}
                  />
                </label>
                <button className="btn btn-primary" type='button' onClick={(e) => this.create(e)}>
                  Add
                </button>
                <button className="btn btn-info" type='button' onClick={(e) => this.update(e)}>
                  Update
                </button>
                <button className="btn btn-danger" type='button' onClick={(e) => this.delete(e)}>
                  Delete
                </button>
              </form>
            </div>
          </div>
          <table>
            <thead>
            <tr>
              <th>ID</th>
              <th>Name</th>
              <th>Surname</th>
            </tr>
            </thead>
            <tbody>
            {this.state && this.state.users.map(users => {
              return <tr>
                <td>{users._id}</td>
                <td>{users.name}</td>
                <td>{users.surname}</td>
              </tr>
            })}
            </tbody>
          </table>
        </div>
    );
  }
}

export default App;



